import './about/About.css';
import React, { useState, useEffect } from 'react';
const Navbar = React.lazy(()=>import('./Navbar'));
const Copyright = React.lazy(()=>import('./Copyright'));

function About(props){
	// hello text change----------------------------------------------------
	const[helloText,setHelloText]=useState(0);
	const [isPaused,setIsPaused]=useState(false);
	  const developer=["Namaste","Hello","Hi"];
	  useEffect(()=>{
	  	if(isPaused) return; // Do not start interval if paused
	  	const interval=setInterval(()=>{
	  		setHelloText((prevIndex)=>(prevIndex+1) % developer.length);
	  	},2000);
	  	return ()=>clearInterval(interval);
	  },[isPaused]);

	  const handleMouseEnter =()=>setIsPaused(true);
	  const handleMouseLeave=()=>setIsPaused(false);

	return(
		<>
		<Navbar/>

		<div className="container-fluid container-fluid-nooverflow">

	      <div className="row justify-content-start align-items-center ">
	      	{/*Heading-----------------------------------------------------------------------*/}
	      	<div className="col-10 col-sm-10 col-md-11 col-lg-11 col-xl-11 col-xxl-11  ">
	        	<h1 className="heading-comman ">{props.heading}<span>ME</span></h1>
	        </div>

	        {/*-------------------------about paragraph-------------------------------*/}
	        <div class="col-10 col-sm-10 col-md-11 col-lg-11 col-xl-11 col-xxl-11 ">
	        	<div className="row justify-content-start align-items-center about-outer-box">
	        		<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 about-img-box">
	        			{/*<p className="about-paragraph ">" Hello, I'm Mukesh Kumar, 
		        			a budding front-end web developer on a mission to learn, grow, 
		        			and make an impact in the digital world. My journey into web development 
		        			began with a passion for technology and a desire to create beautiful and 
		        			functional websites. With a solid foundation in HTML, CSS, and JavaScript, 
		        			I'm eager to dive deeper into the world of front-end development and explore 
		        			new technologies and frameworks. Every project is an opportunity for me to expand 
		        			my skills and showcase my creativity, whether it's designing responsive layouts,
		        			 adding interactive elements, or refining user experiences. I approach 
		        			 each challenge with enthusiasm and a willingness to learn, 
		        			always seeking feedback and striving for improvement.  "
	        			</p>*/}
		        		<p onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
		        			<span>{developer[helloText]}!</span> I’m <span>Mukesh Kumar</span>, a passionate web development enthusiast stepping into 
		        			the tech world with a desire to create impactful digital experiences. I have a 
		        			strong foundation in HTML, CSS, JavaScript, and frameworks like Bootstrap and 
		        			jQuery. I’m also exploring advanced technologies like ReactJS, Core PHP, and 
		        			MySQL, with hands-on experience in version control tools like Git and GitHub.
							Although I’m at the beginning of my journey as a developer, I am dedicated to 
							honing my skills and becoming a solid web developer who can contribute to 
							innovative projects. My curiosity and eagerness to learn drive me to tackle
							 challenges and continuously improve.
		        		</p>
	        		</div>

{/*-------------------------personal-------------------------------*/}
	        		<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
	        			<h1 className="about-heading-common perosnal-heading ">PERSONAL</h1>
	        		</div>

	        		<div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 col-xxl-6 about-personal-item">
	        			Name : <span>{props.about[0]}</span>
	        		</div>

	        		<div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 col-xxl-6 about-personal-item">
						  D.O.B : <span>{props.about[1]}</span>
	        		</div>

	        		<div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 col-xxl-6 about-personal-item">
						  Gender : <span>{props.about[2]}</span>
	        		</div>

	        		<div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 col-xxl-6 about-personal-item">
						  Nationality : <span>{props.about[4]}</span>
	        		</div>	        	

	        		<div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 col-xxl-6 about-personal-item">
						  Language : <span>{props.about[6]}</span>
	        		</div>	


{/*-------------------------interest-------------------------------*/}
	        		<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
	        			<h1 className="about-heading-common ">INTERESTS</h1>
	        		</div>
	        		<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 ">
						<ul className="ul-common">
							<li className="about-interest-item ">Watching and playing cricket</li>
							<li className="about-interest-item ">Create interactive and responsive web sites</li>
						</ul>
	        		</div>

{/*-------------------------Experience-------------------------------*/}
	        		<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
	        			<h1 className="about-heading-common  ">EXPERIENCE</h1>
	        		</div>
	        		<div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 ">
						<ul className="ul-common">
							<li className="about-interest-item ">Apprentice - Excise and Taxation Department, Gurugram - 2020-21</li>
							<li className="about-interest-item ">3 - Front-end web project</li>
							<li className="about-interest-item ">1 - Back-end web project</li>
						</ul>
	        		</div>


	            </div>
	        </div>

	      </div>
	    </div>

	    <Copyright/>
		</>
		);
}
export default About;